/* MOBILE */
@media (max-width: 768px) {

  /* MAIN */
  .main-img-content{
    -o-object-fit: cover;
       object-fit: cover;
    width: 100%;
  }

  /* ALBUM */
  .album-container .cover-img-container{
    width: 100%;
  }
  .cover-img{
    background-position: center;
  }
}



